import React from "react";
import market from "../../config/market";

const ChoosenProductList = React.lazy(() => import("./ChoosenProductList"));
const ChoosenProductListTwo = React.lazy(() =>
	import("./ChoosenProductListTwo")
);

export default function() {
	switch (market.config.choosenProductList) {
		case 1:
			return (
				<ChoosenProductList
					addClass={
						market.config.home === 7
							? "col-lg-3 col-md-4 col-6"
							: null
					}
				/>
			);
		case 2:
			return <ChoosenProductListTwo />;
		default:
			return <ChoosenProductList />;
	}
}
